.htpasswd-form {
  .field-label {
    font-size: var(--pf-c-form__label--FontSize);
  }
  
  .field-radio-control {
    padding-bottom: 8px;
  }
  
  .suggestion {
    font-family: var(--pf-global--FontFamily--monospace);
    border: 1px solid #ccc;
    padding: 1px 8px;
    background-color: #eee;
    cursor: auto;
  } 

  .pf-c-helper-text {
    margin-top: var(--pf-c-form__helper-text--MarginTop);
  }
}
